                        <math class="em">p_rV_{ri}=ZT_r</math>
                        <math inline=1>Z=Z(p_r,T_r)</math><math inline=1>V_{ri}=V(p_r,T_r)</math><math inline=1>(p_r,T_r,V_{ri})</math><math inline=1 class="em">f(p_r,T_r,V_{ri})=0</math><br>
